
import PropTypes from 'prop-types';
import { useRef } from 'react';
import Slider from 'react-slick';
// @mui
import { useTheme } from '@mui/material/styles';
import { Box, Stack, Paper, Grid, Button, Avatar, Container, Typography } from '@mui/material';

// components
import Image from '../../components/Image';
import Label from '../../components/Label';
import TextMaxLine from '../../components/TextMaxLine';
import { MotionInView, varFade } from '../../components/animate';

import _mock, { _carouselsMembers } from '../../_mock';

// ----------------------------------------------------------------------
const _recommendShows = [...Array(6)].map((_, index) => ({
  cover: _mock.image.cover(index),
  title: _mock.text.title(index),
  name: _mock.name.fullName(index),
  avatar: _mock.image.avatar(index),
  price: _mock.number.price(index),
}));
const _recommendProduct = [...Array(5)].map((_, index) => ({
  cover: _mock.image.product(index),
  title: _mock.text.title(index),
  name: _mock.name.fullName(index),
  avatar: _mock.image.avatar(index),
  price: _mock.number.price(index),
}));


// ----------------------------------------------------------------------

// 

export default function LanRecents() {
  const theme = useTheme();
  const _recentData = _recommendProduct.concat(_recommendShows);

  return (

    <Box sx={{ pb: 2 }}>
      <MotionInView variants={varFade().inUp}>
        <Typography variant="h4" sx={{ mb: 1 }}>
          {"Recent Shows And Products"}
        </Typography>
      </MotionInView>
      <Grid container spacing={3}>
        {_recentData.map((item, index) => (
          <Grid item key={index} xs={12} sm={6} md={4} lg={3}>
            <ShowCard item={item} />
          </Grid>
        ))}
      </Grid>
     
    </Box>
  );
}

// ----------------------------------------------------------------------

ShowCard.propTypes = {
  item: PropTypes.shape({
    avatar: PropTypes.string,
    name: PropTypes.string,
    price: PropTypes.number,
    cover: PropTypes.string,
    title: PropTypes.string,
  }),
};


function ShowCard({ item }) {
  const { avatar, name, title, cover, price } = item;
  const theme = useTheme();
  return (
    <Paper sx={{
       borderRadius: 1, bgcolor: 'background.neutral',
      '&:hover': { boxShadow: `0px 4px 10px 3px ${theme.palette.grey[500_80]}` }
    }}>
      <Stack spacing={2.5} sx={{ px: 3, py: 1 }}>
        <Stack direction="row" alignItems="center" spacing={2}>
          <Avatar alt={name} src={avatar} width={14} height={14} />
          <div>
            <TextMaxLine line={1}>{title}</TextMaxLine>
            <Typography variant="caption" sx={{ color: 'text.disabled', mt: 0.5, display: 'block' }}>
              {name}
            </Typography>
          </div>
        </Stack>
      </Stack>

      <Box sx={{ px: 1, position: 'relative' }}>
        <Label
          variant="filled"
          color={"primary"}
          sx={{ right: 16, zIndex: 9, bottom: 50, position: 'absolute', textTransform: 'capitalize', }}
        >
          {price}
        </Label>
        <Image src={cover} ratio="16/9" sx={{ borderRadius: 1 }} />
        <TextMaxLine line={1} sx={{ py: 0.5, px: 2 }}>{title}</TextMaxLine>
      </Box>
    </Paper>
  );
}
