import PropTypes from 'prop-types';
import { useRef } from 'react';
import { useNavigate } from 'react-router';
import Slider from 'react-slick';
// @mui
import { useTheme } from '@mui/material/styles';
import { Box, Stack, Paper, Button, Avatar, Container, Typography } from '@mui/material';

// components
import Image from '../../components/Image';
import Label from '../../components/Label';
import { CarouselArrows } from '../../components/carousel';
import TextMaxLine from '../../components/TextMaxLine';
import { MotionInView, varFade } from '../../components/animate';



// ----------------------------------------------------------------------

LanCommonSection.propTypes={
  cardData:PropTypes.array.isRequired,
  sectionTitle:PropTypes.string,
}


export default function LanCommonSection({ cardData, sectionTitle }) {
  const carouselRef = useRef(null);
  const theme = useTheme();
  const navigate = useNavigate();
  const onCardClick = (item, index)=>{
    console.log(item);
    if(item.type === "product")
      navigate(`/live-ecommerce/product/${item.id}`);
    else
      navigate(`/live-ecommerce/liveshow/${item.id}`);
      
  }


  const settings = {
    arrows: false,
    slidesToShow: 4,
    centerMode: true,
    centerPadding: '0px',
    rtl: Boolean(theme.direction === 'rtl'),
    responsive: [
      {
        breakpoint: 1279,
        settings: { slidesToShow: 3 },
      },
      {
        breakpoint: 959,
        settings: { slidesToShow: 2 },
      },
      {
        breakpoint: 600,
        settings: { slidesToShow: 1 },
      },
    ],
  };

  const handlePrevious = () => {
    carouselRef.current.slickPrev();
  };

  const handleNext = () => {
    carouselRef.current.slickNext();
  };

  return (
    <Box sx={{ pb: 2 }}>
      <MotionInView variants={varFade().inUp}>
        <Typography variant="h4" sx={{ mb: 1 }}>
          {sectionTitle}
        </Typography>
      </MotionInView>
      <Box sx={{ position: 'relative' }}>
        <CarouselArrows filled onNext={handleNext} onPrevious={handlePrevious}>
          <Slider ref={carouselRef} {...settings}>
            {cardData.map((item, index) => (
              
                <ShowCard key={index} item={item} 
                handleClick = {()=>onCardClick(item, index)}
                />
              
            ))}
          </Slider>
        </CarouselArrows>
      </Box>
    </Box>
  );
}

// ----------------------------------------------------------------------

ShowCard.propTypes = {
  handleClick:PropTypes.func,
  item: PropTypes.shape({
    avatar: PropTypes.string,
    name: PropTypes.string,
    price: PropTypes.number,
    cover: PropTypes.string,
    title: PropTypes.string,
  }),
};


function ShowCard({ item,handleClick }) {
  const { avatar, name, title, cover, price } = item;
  const theme =useTheme();
  return (
    <Paper sx={{ mx: 1,my:4, borderRadius: 1, bgcolor: 'background.neutral',
   '&:hover':{boxShadow:`0px 4px 10px 3px ${theme.palette.grey[500_80]}`} }}
   onClick={handleClick}
   >
      <Stack spacing={2.5} sx={{ px: 3, py:1 }}>
        <Stack direction="row" alignItems="center" spacing={2}>
          <Avatar alt={name} src={avatar} width={14} height={14} />
          <div>   
            <TextMaxLine line={1}>{title}</TextMaxLine>
            <Typography variant="caption" sx={{ color: 'text.disabled', mt: 0.5, display: 'block' }}>
              {name}
            </Typography>
          </div>
        </Stack>
      </Stack>

      <Box sx={{ px: 1, position: 'relative' }}>
        <Label
          variant="filled"
          color={"primary"}
          sx={{ right: 16, zIndex: 9, bottom: 50, position: 'absolute', textTransform: 'capitalize', }}
        >
          {price}
        </Label>
        <Image src={cover} ratio="16/9" sx={{ borderRadius: 1 }} />
        <TextMaxLine line={1} sx={{ py: 0.5, px: 2 }}>{title}</TextMaxLine>
      </Box>
    </Paper>
  );
}
