import PropTypes from 'prop-types';
import { useRef } from 'react';

import Slider from 'react-slick';
// @mui
import { useTheme } from '@mui/material/styles';
import { Box, Stack, Paper, Button, Avatar, Container, Typography } from '@mui/material';
// _mock
import _mock from '../../_mock';
// components
import Image from '../../components/Image';
import { CarouselArrows } from '../../components/carousel';
import TextMaxLine from '../../components/TextMaxLine';
import { MotionInView, varFade } from '../../components/animate';
// utils
// hooks
// import useRouter from '../../hooks/useRouter';
// ----------------------------------------------------------------------

const popularChannels = [...Array(6)].map((_, index) => ({
  id: _mock.id(index),
  cover: _mock.image.cover(index),
  title: _mock.text.title(index),
  name: _mock.name.fullName(index),
  avatar: _mock.image.avatar(index),
  
}));


export default function UpComings() {
  const carouselRef = useRef(null);
  const theme = useTheme();



  const settings = {
    arrows: false,
    slidesToShow: 4,
    centerMode: true,
    centerPadding: '0px',
    rtl: Boolean(theme.direction === 'rtl'),
    responsive: [
      {
        breakpoint: 1279,
        settings: { slidesToShow: 3 },
      },
      {
        breakpoint: 959,
        settings: { slidesToShow: 2 },
      },
      {
        breakpoint: 600,
        settings: { slidesToShow: 1 },
      },
    ],
  };

  const handlePrevious = () => {
    carouselRef.current.slickPrev();
  };

  const handleNext = () => {
    carouselRef.current.slickNext();
  };

  return (
    <Box sx={{ pb: 2 }}>
      <MotionInView variants={varFade().inUp}>
        <Typography variant="h4" sx={{ mb: 1 }}>
          Popular Channels
        </Typography>
      </MotionInView>
      <Box sx={{ position: 'relative' }}>
        <CarouselArrows filled onNext={handleNext} onPrevious={handlePrevious}>
          <Slider ref={carouselRef} {...settings}>
            {popularChannels.map((item, index) => (
              <MotionInView key={index} variants={varFade().in} sx={{ px: 1.5, py: 1 }}>
                <ShowCard item={item} />
              </MotionInView>
            ))}
          </Slider>
        </CarouselArrows>
      </Box>
    </Box>
  );
}

// ----------------------------------------------------------------------

ShowCard.propTypes = {
  item: PropTypes.object.isRequired,

};



function ShowCard({ item }) {
  const theme = useTheme();
  const { avatar, name, title, cover } = item;
  // const router =useRouter();
  return (
    <Paper sx={{ my:2, borderRadius: 1, bgcolor: 'background.neutral',
    '&:hover':{boxShadow:`0px 4px 10px 3px ${theme.palette.grey[500_80]}`} }}>
      <Box sx={{  position: 'relative' }}>

        <Image src={cover} ratio="1/1" sx={{ borderRadius: 1 }} />


        <Stack sx={{ right: 16, zIndex: 9, top: 10, position: 'absolute', }}>
            <Button variant="outlined" sx={{px:1,py:0.2}} >follow</Button>
         
        </Stack>
        <Stack spacing={2.5} sx={{ left: 16, zIndex: 9, bottom: 10, position: 'absolute', }}>
          <Stack direction="row" alignItems="center" spacing={2} sx={{pr:5}}>
            <Avatar alt={name} src={avatar} />
            <div>
              <TextMaxLine sx={{color:'#fff'}} line={1}>{title}</TextMaxLine>
              <Typography  sx={{ color: '#fff', display: 'block' }}>
                {name}
              </Typography>
            </div>
          </Stack>
        </Stack>

      </Box>
    </Paper>
  );
}


