import { useRef } from 'react';
import Slider from 'react-slick';
// @mui
import { useTheme } from '@mui/material/styles';
import { Box, Grid, Paper, Button, Avatar, Container, Typography } from '@mui/material';
// _mock
import _mock from '../../_mock';
// components
import Image from '../../components/Image';
import { CarouselDots } from '../../components/carousel';

const video = {
  light: _mock.image.product(7),
  video: "https://media.w3.org/2010/05/sintel/trailer_hd.mp4"
}


// ----------------------------------------------------------------------



export default function LanVideo() {
  const carouselRef = useRef(null);
  const theme = useTheme();

  const settings = {
    dots: true,
    arrows: true,
    autoplay: true,
    slidesToShow: 1,
    rtl: Boolean(theme.direction === 'rtl'),
    ...CarouselDots({
      rounded: true,
      sx: { mt: 3 },
    }),
  };


  return (

    <Grid container spacing={5} justifyContent="space-between">
      <Grid item xs={12} sm={5}>
        <Box sx={{ position: 'relative', p: 3 }}>
          <Slider ref={carouselRef} {...settings}>
            <Box key={"join-1"}>
              <Typography variant="h4" sx={{}}>
                WELCOME TO YOUR
              </Typography>
              <Typography variant="h4" sx={{}}>
                SOCIAL-SELLING
              </Typography>
              <Typography variant="h4" sx={{}}>
                NETWORK!
              </Typography>
              <Typography sx={{}}>
                Weve built the first online platform for live streaming and home shopping shows with a built-in buying experience that anyone can use.
              </Typography>
              <Button variant="contained" sx={{ mt: 3 }}>Start Streaming For Free</Button>
            </Box>
            <Box key={"join-2"}>
              <Typography variant="h4" sx={{}}>
                THE SHOPPING
              </Typography>
              <Typography variant="h4" sx={{ color: theme.primary }}>
                EXPERIENCE EVERYONE
              </Typography>
              <Typography variant="h4" sx={{}}>
                IS TALKING ABOUT!
              </Typography>
              <Typography sx={{}}>
                An always live social - shopping network, with your favorite brands, artists, celebrities and useful products.
              </Typography>
              <Button variant='contained' sx={{ mt: 3 }}>join For Free</Button>
            </Box>

          </Slider>
        </Box>
      </Grid>
      <Grid item xs={12} sm={7} >
      <Box sx={{ width: "100%", maxHeight: "60vh" ,height:"auto", position: 'relative',backgroundColor:'black' }}>
        <video controls muted autoPlay
          width="100%" height="100%"
          poster={video.light}
        >
          <source src={video.video} type="video/mp4" />
        </video>
        </Box>
      </Grid>
    </Grid>

  );
}

// ----------------------------------------------------------------------



