import { m } from 'framer-motion';
import PropTypes from 'prop-types';
// @mui
import { styled } from '@mui/material/styles';
import { Button, Box, Stack, Grid, ImageList, ImageListItem } from '@mui/material';
// components
import { LazyLoadImage } from 'react-lazy-load-image-component';
import Image from '../../components/Image';

// hook
import useResponsive from '../../hooks/useResponsive';
import _mock from '../../_mock'
// ----------------------------------------------------------------------


const heroImages = [...Array(9)].map((_, index) => ({
  cover: _mock.image.cover(index),
  title: _mock.text.title(index),
  name: _mock.name.fullName(index),
  avatar: _mock.image.avatar(index),
  price: _mock.number.price(index),
}));

const channelMark = "/static/hero.jpg";
const thumnail = _mock.image.product(24);
//------------------------------------------------------------------------


export default function LanHero() {
  const isTablet = useResponsive('down', 'md');
  return (

    <Grid container spacing={4} sx={{ mb: 5 }} justifyContent="space-between">

      <Grid item xs={6} sm={6} md={3}>
        <Image src={heroImages[0].cover} ratio="4/3" sx={{ borderRadius: 1 }} />
      </Grid>

      {isTablet &&
        <Grid item xs={6} sm={6} md={3}  >
          <Box sx={{ textAlign: 'center' }} >
            <LazyLoadImage src={channelMark} width="60%" height="auto" style={{ margin: "auto" }} />
            <Button variant='contained'>See Who's Streaming</Button>
          </Box>
        </Grid>}

      <Grid item xs={12} sm={12} md={6} >
        <ImageList variant="quilted" gap={10} cols={ 4} rows={ 2}>
          {heroImages.map((item, index) => (
            (index > 0) &&
            <ImageListItem key={index}>
              <Image src={item.cover} ratio="4/3" sx={{ borderRadius: 1 }} />
            </ImageListItem>
          ))}
        </ImageList>
      </Grid>

      {!isTablet &&
        <Grid item xs={6} sm={6} md={3}  >
          <Box sx={{ textAlign: 'center' }} >
            <LazyLoadImage src={channelMark} width="60%" height="auto" style={{ margin: "auto" }} />
            <Button variant='contained' sx = {{mt:2}}>See Who's Streaming</Button>
          </Box>
        </Grid>}
    </Grid>

  )
}


