import LanCommonSection from './LanCommonSection';
import _mock from '../../_mock';

// ----------------------------------------------------------------------
const _recommendProducts = [...Array(10)].map((_, index) => ({
  cover: _mock.image.product(index),
  title: _mock.text.title(index),
  name: _mock.name.fullName(index),
  avatar: _mock.image.avatar(index),
  price: _mock.number.price(index),
  id:_mock.id(index),
  type:"product",
}));

export default function LanRecommendProducts(){
  return(
    <LanCommonSection sectionTitle={'Recommend Products'} cardData={_recommendProducts}/>
  )
}
