
import _mock, { _carouselsMembers } from '../../_mock';
import LanCommonSection from './LanCommonSection';

// ----------------------------------------------------------------------
const _recommendShows = [...Array(10)].map((_, index) => ({
  cover: _mock.image.cover(index),
  title: _mock.text.title(index),
  name: _mock.name.fullName(index),
  avatar: _mock.image.avatar(index),
  price: _mock.number.price(index),
  id:_mock.id(index),
  type:"shows",

}));


export default function LanRecommendShows() {
  return(
    <LanCommonSection sectionTitle={"Recommend Shows"} cardData={_recommendShows}/>
  )
}

